    "learned_computing": "기존에 소프트웨어 교육을 받아보셨나요?",
    "cparty_index_description_1": "두근두근 소프트웨어와 첫 만남.",
    "cparty_index_description_2": "소프트웨어랑 재미있게 놀다 보면 소프트웨어의 원리도 배우고,  생각하는 힘도 쑥쑥!",
